<script>
import { ref } from "vue";
import Header from "../../components/Header.vue";

const setup = {
 // url: `https://agpublick.com.br/invet/api/clients`,
  origin: process.env.VUE_APP_URL_API_ORIGIN,
  url: process.env.VUE_APP_URL_API_CLIENTS
};

const columns = [
  {
    name: 'name',
    required: true,
    label: 'Nome',
    align: 'left',
    field: 'name',
    sortable: true
  },
  { name: 'pets', label: 'pet(s)', align: 'left', field: 'pets' },
  { name: 'edit', label: '', field: 'edit', color: 'red' }

]

const rows = [
  {
    name: '',
    pets: '',
    edit: ''
  }
]

export default {
  name: "Home",
  search: "Pesquisar",
  components: {
    Header,
  },
  setup() {
    return {
      setup: setup,
      filter: ref(""),
      filterCols: ['name','cpf'],
      dense: ref(false),
      columns,
      rows: ref(rows)
    };
  },
  data() {
    return {
      loading: false,
      pageTitle: "Lista de Clientes",
      btnAddLabel: "",
      btnAddIcon: "las la-user-plus",
      btnAddToolTiplabel: "Cadastrar novo cliente",
      searchPlaceholder: "Pesquisar",
      status: null,
      data: null,
      client: null,
      del: null,
      patch: null,
    };
  },
  methods: {
    async getClientes() {
      const req = await fetch(this.setup.url);
      const jsonClients = await req.json();
      this.status = jsonClients.status;
      this.data = jsonClients.data.clients;
      this.rows = this.data;
      console.log(this.data);
    },
    async deleteClient(id) {
      const data = {
        'id': id
      }
      const req = await fetch(this.setup.url, {
        method: 'DELETE', // *GET, POST, PUT, DELETE, etc.
          mode: 'cors',
          credentials: 'same-origin',
          headers: {
            'Authorization':'Bearer ' + localStorage.getItem('invet_user_token_HtRE4'),
            'Origin': '*',
            'Accept': 'application/json, text/javascript, */*; q=0.01',
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          },  
        //mode: 'cors', // no-cors, *cors, same-origin
        //cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
        //credentials: 'same-origin', // include, *same-origin, om [it
        //headers: {
          //'Origin': 'http://localhost:8080/',
          //'Accept': 'application/json, text/javascript, */*; q=0.01',
          //'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          //'Content-Type': 'application/json'
          //'Content-Type': 'application/x-www-form-urlencoded',
        //},
        //redirect: 'follow',
       // referrerPolicy: 'no-referrer',
        body: JSON.stringify(data),
      });

      this.del = await req.json();
      console.log(this.del);
      this.getClientes();
    },
    async updatedClient(event, id) {
      const option = event.target;

      const dataJson = JSON.stringify({ status: option });

      const req = await fetch(this.setup.url +'/'+ id, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: dataJson,
      });

      this.patch = await req.json();
      console.log(this.patch);
    },
    customFilter(rows, terms) {
        const lowerTerms = terms ? terms.toLowerCase() : ''
        const toFilter = this.byVisibility ? [...this.visibleColumns, ...this.reqs ] : this.filterCols
        
        const filteredRows = rows.filter(
          row => toFilter.some(col => (row[col] + '').toLowerCase().includes(lowerTerms))
        )
        return filteredRows
      
    },
  },
  mounted() {
    this.getClientes();
  },
  computed: {
     reqs() {
       return this.columns.reduce((a, o) =>{
          o.required && a.push(o.name)
          return a
        },[])
     }
  },
};
</script>
